@use 'variables' as v;


@mixin phone {
  @media(min-width: v.$phone){
    @content;
  }
}
@mixin tablet {
  @media(min-width: v.$tablet){
    @content;
  }
}

@mixin pc{
  @media(min-width: v.$pc){
    @content;
  }
}

@mixin monitor{
  @media(min-width: v.$monitor){
    @content;
  }
}


//display grid
@mixin grid($columnas,$gap){
  display:grid;
  grid-template-columns: repeat($columnas,1fr);
  column-gap:$gap;
}

@mixin after ($color){

  display: inline-block;
  &::after {
    margin: -1.2rem 0 0;
    display: block;
    background-color: $color;
    content: '';
    height: .8rem;
    width: calc(100% + 1rem);
  }
}
