@use '../base/mixins' as m;
@use '../base/variables' as v;


.beneficios {
  background-color: v.$verde;
  padding: 5rem 0;
}

.beneficios__grid{

  @include m.tablet {
    @include m.grid(3, 4rem);
    align-items: center;
  }


}
.beneficios__contenido {
  grid-column: 1/3;
}
.beneficios__title {
  display: inline-block;

  &::after {
    content: '';
    height: 1rem;
    background-color: v.$blanco;
    display: block;
    margin: -1.4rem 0 0 0;
     width: calc(100% + 1rem);
    
  }
}

.beneficios__imagen {
  
  transition-property: transform;
  transition-duration: .6s;

  &:hover {
    transform: scale(1.1) rotate(4deg);
  }
}