
@use 'variables' as v;


html{
  font-size: 62.5%;
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: inherit;
}


body {
  overflow-x: hidden;
  font-size: 1.6rem;

  
}
a {
  text-decoration: none;
  font-size: 2rem;
}
p{
  font-size: 1.7rem;
  line-height: 1.8;
  text-align: left;
}


h1,h2,h3 {

  color: v.$negro;
  margin:0 0 1rem;
}

h1 {
  font-size:5rem;
}

h2 {
  
  font-size: 3.5rem;
}

h3 {
  font-size: 2.5rem;
}

img {
  display: block;
  max-width:100%;
  height: auto;
  margin:0 auto;
  
}

span{
  font-weight: bold;
}

a{
  text-decoration: none;
  font-size: 2rem;
}

li {
 list-style: none;
}

a.activo {

}

.contenedor {
  width: 90%;
  max-width:120rem;
  margin: 0 auto;
  text-align: center;
}



