@use '../base/mixins' as m;
@use '../base/variables' as v;


.planes {
  padding: 9rem 0;
  background-color: v.$negro;
  color: v.$blanco;
}

.planes__title {
  color: v.$blanco;
  @include m.after(v.$verde)
}

.planes__grid {
  display: flex;
  flex-direction: column;


  @include m.tablet {
    @include m.grid(3, 0rem);
  }
}

.planes__text {
 margin-bottom: 6rem;
}

.plan {
  border-bottom: 1px solid rgba(163, 163, 163, 0.414);
  padding: 16rem 3rem 5rem;
  color: v.$negro;
  background-color: v.$blanco;
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;

  &:first-child{
    background-image: url(../img/icono_inicio.svg);
  }
  &:nth-child(2 ){
    background-image: url(../img/icono_negocio.svg);
    order:-1
  }

  @include m.tablet {
    &:nth-child(2 ){
      order:0;
      z-index: 100;
    }
  }

  &:last-child {
    background-image: url(../img/icono_pro.svg);
  }


  @include m.tablet {
    transition-property: transform;
    transition-duration: .4s;

    &:hover {
      transform: scale(1.1);
    }

  }

}

.plan--pro {
  background-color: v.$verde;

  @include m.tablet {
    box-shadow: 0px 1px 9px -2px  v.$negro;
    border-radius: 10px;
    margin-bottom: -5rem;
    margin-top: -5rem;
  }

}

.plan__title {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-transform: uppercase;
}

.plan__precio {
  font-family: v.$title;;
  font-weight: bold;
  text-align: center;
  margin:1.5rem 0;
}

.plan__lista {
  text-align: center;
  padding-left: 0;
  padding: 1rem;
  line-height: 2;
  
}

.plan__inscribirse {
  font-family: v.$title;
  font-weight: bold;
  color: v.$negro;
  padding: .6rem 1rem;
  background-color: v.$verde;

  &:hover {
    background-color: v.$Oscuro
  }
}

.plan__inscribirse--blanco {
  border: 1px solid v.$blanco;
}