@use '../base/mixins' as m;
@use '../base/variables' as v;


.footer {
  background-color: v.$negro;
}

.footer__contenido  {
  padding: 3rem 0;
 

}

.footer__grid {
  @include m.tablet {
    @include m.grid (4, 2rem);
    align-items: center;
  }
}

.footer__card {
  padding: 3rem 0 0 0;
}

.footer__imagen {
  margin: 0 auto;

  @include m.tablet {
    margin:0;
  }

}

.card__title {
  font-family: v.$title;
  text-transform: uppercase;
  color: v.$verde;;
}

.card__enlace {
  padding: .4rem;
  color:v.$blanco;
  display: block;
}

.footer__creditos {
  border-top: 2px solid v.$verde;
  font-family: v.$title;
  font-size: 1.5rem;
  text-transform: uppercase;
  margin: 0;
  padding: 1rem;
  text-align: center;
  color: v.$blanco;
}