@use '../base/mixins' as m;
@use '../base/variables' as v;

.testimoniales {
  padding: 5rem 0;
  @include m.tablet {
    @include m.grid(2, 2rem);
    align-items: center;
    
  }
}

.testimoniales__imagen {
 margin-bottom: 3rem;
}

.testimoniales__testimonios {

}

.testimoniales__title {
  @include m.after (v.$verde);
}

.testimonios__grid {


}

.testimonio{
  border-radius: 10px;
  box-shadow: 1px 1px 2px #26303f;
  padding: 2rem 2rem 2rem 4rem;
  background-color: v.$verde;
  margin-bottom: 1rem;
  position: relative;

}

.testimonio__info {
  font-size: 1.5rem;
  line-height: 1.5;

  &::before {
    background-image: url(../img/comilla.svg);
    position: absolute;
    content: '';
    width: 2rem;
    height: 2rem;
    background-repeat: no-repeat;
    top: 4rem;
    left: 1.2rem;

   }
}

.testimonio__author {
  color: v.$blanco;
  padding: 0 1rem;
  border-radius: 5px;
  margin: 0;

  font-family: v.$title;
  font-weight: bold;
  text-align: right;
}