@use 'base/mixins' as m;
@use 'base/variables' as v;


.header {
  background-image: linear-gradient(rgba(25, 29, 35, 0.7),rgba(31, 48, 72, 0.7)),url(../img/header.jpg);
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 7rem 0;
}

.header__contenido {
 

}

.header__imagen {
  margin:0 auto;
  max-width: 25rem;

  @include m.phone {
    margin: 0;;
  }
}

.header__info {
  padding: 10rem 0;
  text-align: left;
}

.header__mensaje {
  max-width: 60%;
  font-weight: bold;
  color: v.$blanco;
  font-size:6rem;
}

.header__crearCuenta {
  font-family: v.$title;
  display: block;
  box-shadow: 3px 1px 30px -12px rgb(0, 0, 0);
  border: 1.5px solid v.$Oscuro;
  text-align: center;
  font-size: 2.2rem;
  font-weight: bold;
  border-radius: 5px;
  color: v.$negro;
  padding: .7rem 2.5rem ;
  background-color: v.$verde;

  &:hover {
    background-color: v.$Oscuro;

  }

  @include m.phone {
    display: inline;

  }
}